    </div>
    <div class="wrap mb30 company-desc-div">
      <h2 class="tc">安防监控</h2>
      <p
        class="text-muted"
      >安防监控系统是应用光纤、同轴电缆或微波在其闭合的环路内传输视频信号，并从摄像到图像显示和记录构成独立完整的系统。它能实时、形象、真实地反映被监控对象，不但极大地延长了人眼的观察距离，而且扩大了人眼的机能，它可以在恶劣的环境下代替人工进行长时间监视，让人能够看到被监视现场的实际发生的一切情况，并通过录像机记录下来。同时报警系统设备对非法入侵进行报警，产生的报警信号输入报警主机，报警主机触发监控系统录像并记录。</p>
      <p
        class="text-muted"
      >安防监控系统中提高压缩效率、提高鲁棒性和错误恢复能力等，以及减少实时的时延、减少信道获取时间和随机接入时延、降低复杂度等。都是个重要的环节，追求高清成像的安防监控，将视频的效果在传输与处理过程中保障到极致。对于如何来选择优质的安防监控系统产品上应用的电子元器件，如贴片电容，高压贴片电容，贴片电阻，贴片电感，钽电容，精密电阻，电解电容，稳压二极管，整流二极管，发光二极管等电子元器件。对安防监控系统产品的性能参数的提高起着重要和决定性作用。</p>
