<template>
  <div>
    <div class="senbao-banner">
      <img src="static/images/banner/banner_product_application.png" alt />
    </div>
    <div class="tc sbhq-crumb-div">
      <sbhq-wrap-nav :selectedId="selectedId" :navArray="navArray"></sbhq-wrap-nav>
      <!-- <product-application-nav></product-application-nav>
      <div class="crumb col-xs-3 tr">
        <router-link class="color-gray8" :to="{name:'SenbaoIndex'}">首页</router-link>
        <i class="glyphicon glyphicon-menu-right color-gray8"></i>
        <router-link class="color-gray8" :to="{name:'securityControl'}">产品应用</router-link>
        <i class="glyphicon glyphicon-menu-right color-gray8"></i>
        <span class>汽车电子</span>
      </div> -->
    </div>
    <div class="wrap mb30 company-desc-div">
      <h2 class="tc">汽车电子</h2>
      <p
        class="text-muted"
      >汽车电子是车体汽车电子控制装置和车载汽车电子控制装置的总称。车体汽车电子控制装置，包括发动机控制系统、底盘控制系统和车身电子控制系统（车身电子ECU）。汽车电子最重要的作用是提高汽车的安全性、舒适性、经济性和娱乐性。用传感器、微处理器MPU、执行器、数十甚至上百个电子元器件及其零部件组成的电控系统。在汽车环境中使用电子产品的数量不断增长，无源器件拥有先进的产品特性和卓越的可靠性以及广泛的多样性。因此如何来选择优质的汽车产品上应用的电子元器件，如贴片电容，高压贴片电容，贴片电阻，贴片电感，钽电容，精密电阻，电解电容，稳压二极管，整流二极管，发光二极管等电子元器件。对汽车产品的性能参数的提高起着重要和决定性作用。国巨的电子元件具有高信赖性，并利用陶瓷的耐热优点有助汽车电子控制技术的开发，并整合电子与机载系统。国巨提供节能应用的积层陶瓷电容，具有绝佳低损耗和高信赖性的连接特性，以缩小电动车与混合动力车中转换器和带电辅助装置的尺寸，达到更好的节能效果。</p>
    </div>
  </div>
