      </div>-->
    </div>
    <div class="wrap mb30 company-desc-div">
      <h2 class="tc">数码产品</h2>
      <p
        class="text-muted"
      >数码产品一般指的是MP3、U盘，智能手机，数码照相机/摄像机/扫描仪等可以通数字和编码进行操作的机器并且可以与电脑连接的。对于数码照相机的来说，除了镜头和感光器之外，影像处理器也是至关重要的。感光元件是数码相机的核心，也是最关键的技术。数码相机的核心成像部件有两种：一种是广泛使用的CCD（电荷藕合）元件；另一种是CMOS（互补金属氧化物导体）器件。传统CCD中的每个像素由一个二极管、控制信号路径和电量传输路径组成。SUPER CCD采用蜂窝状的八边二极管，原有的控制信号路径被取消了，只需要一个方向的电量传输路径即可，感光二极管就有更多的空间。 随着半导体技术的不断进步，许多新颖、时尚的便携式电子产品呈现在世人面前，这些便携式产品对电子元器件提出了更高要求，要求微功耗，高可靠，微小型化。如何来选择优质的数码产品上应用的电子元器件，如贴片电容，高压贴片电容，贴片电阻，贴片电感，钽电容，精密电阻，电解电容，稳压二极管，三极管、MOS管等电子元器件。对数码产品的性能参数的提高起着重要和决定性作用。</p>
    </div>
  </div>
</template>
