<template>
  <ul class="sbhq-wrap-nav">
    <!-- <li>
      <router-link :to="{name:'contactUs'}">联系我们</router-link>
    </li>
   <li>
      <router-link :to="{name:'carElectronic'}">在线留言</router-link>
    </li>-->
    <li
      v-for="item in navArray"
      :key="item.navId"
      :id="item.navId"
      :class="selectedId==item.navId?'wrap-nav-active':''"
      @click.stop="gotoPage($event,item.navRouteName)"
    >{{item.navName}}</li>
  </ul>
</template>
<script>
export default {
  props: {
    selectedId: { type: String },
    navArray: { type: Array }
  },
  computed: {
    selectedNum() {
      return this.selectedId;
    }
  },
  data() {
    return {
      // selectedId: "001"
      // navArray: [{
      // 		navId: "001",
      // 		navName: "公司简介",
      // 		navRouteName: ""
      // 	},
      // 	{
      // 		navId: "002",
      // 		navName: "公司简介",
      // 		navRouteName: ""
      // 	}
      // ]
    };
  },
  methods: {
    gotoPage(e, pathName) {
      //console.log(e, pathName, e.target);
      //this.selectedId = e.target.getAttribute("id");
      //console.log( this.$route)
      this.$route.name == pathName ? "" : this.$router.push({ name: pathName });
    }
  }
};
</script>
<style type="text/css">
/* .sbhq-wrap-nav li {
  display: inline-block;
  color: #333;
  font-size: 14px;
  padding: 8px 20px;
  border: 1px solid transparent;
  border-radius: 50px;
  margin: 0 24px;
  cursor: pointer;
}

.sbhq-wrap-nav li:hover {
  color: #059fe9;
  border: 1px solid #059fe9;
}

.sbhq-wrap-nav .wrap-nav-active {
  color: #fff;
  border: 1px solid #059fe9;
  background-color: rgb(0, 128, 255);
}

.sbhq-wrap-nav .wrap-nav-active:hover {
  color: #fff;
} */
</style>


