<template>
  <div>
    <div class="senbao-banner">
      <img src="static/images/banner/banner_product_application.png" alt />
    </div>
    <div class="tc sbhq-crumb-div">
       <sbhq-wrap-nav :selectedId="selectedId" :navArray="navArray"></sbhq-wrap-nav>
      <!-- <product-application-nav></product-application-nav>
      <div class="crumb col-xs-3 tr">
        <router-link class="color-gray8" :to="{name:'SenbaoIndex'}">首页</router-link>
        <i class="glyphicon glyphicon-menu-right color-gray8"></i>
        <router-link class="color-gray8" :to="{name:'securityControl'}">产品应用</router-link>
        <i class="glyphicon glyphicon-menu-right color-gray8"></i>
        <span class>工控设备</span>
      </div> -->
    </div>
    <div class="wrap mb30 company-desc-div">
      <h2 class="tc">工控设备</h2>
      <p
        class="text-muted"
      >工控指的是工业自动化控制，主要利用电子电气、机械、软件组合实现。即是工业控制（Factory control），或者是工厂自动化控制（Factory Automation control）。主要是指使用计算机技术，微电子技术，电气手段，使工厂的生产和制造过程更加自动化、效率化、精确化，并具有可控性及可视性</p>
      <p
        class="text-muted"
      >工控技术的出现和推广带来了 第三次工业革命，使工厂的生产速度和效率提高了300%以上。20世纪80年代初，随着改革开放的春风，国外先进的工控技术进入 中国大陆，比较广泛使用的工业控制产品有“PLC，变频器，触摸屏，伺服电机，工控机”等。</p>
      <p
        class="text-muted"
      >对于如何来选择优质的工控设备产品上应用的电子元器件，如贴片电容，高压贴片电容，贴片电阻，贴片电感，钽电容，精密电阻，电解电容，稳压二极管，整流二极管，发光二极管等电子元器件。对工控设备产品的性能参数的提高起着重要和决定性作用。</p>
    </div>
  </div>
