import Vue from 'vue'
import App from './App'
import router from './router'
import '../static/css/main.css'

Vue.config.productionTip = false

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  mounted(){
    
  },
  render: h => h(App)
})


router.afterEach((to,from)=>{
  window.scroll(0,0)
})

window.onload = function(){
  $('.carousel').carousel({
    interval: 3000
  })
}


